.about {
    padding: 0 0 60px 0;
  
    overflow-x: hidden;
    background: #F8F8F8;
  
    @media screen and (min-width: 1290px) {
      padding: 70px 0;
    }
  
    &__title {
      margin: 0 0 60px;
      color: #2B2929;
      line-height: 1.2;
      font-size: 40px;
      font-weight: 600;
      text-align: center;
    }
  
    &__list {
      display: flex;
      // flex-direction: column;
      justify-content: center;
      flex-wrap: wrap;
      gap: 30px;
  
      // @media screen and (min-width: 1290px) {
      //   flex-direction: row;
      //   flex-wrap: nowrap;
      // }
    }
  
    &__item {
      display: flex;
      align-items: center;
      flex-direction: column;
      width: 300px;
    }
  
    &__icon {
      width: 170px;
      height: 170px;
    }
  
    &__subtitle {
      margin: 20px 0;
  
      color: #2B2929;
      line-height: 1.3;
      font-size: 26px;
      font-weight: 600;
      text-align: center;
    }
  
    &__text {
      color: #2B2929;
      line-height: 1.87;
      font-size: 16px;
      font-weight: 400;
      text-align: center;
    }
  }
  