    bottom: -2px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: $primary-color;
    animation: slideIn 0.3s forwards;
  }
}

@keyframes slideIn {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}

//    
@keyframes burgerRotate {
